import React from 'react';
import * as FontAwesome from 'react-icons/lib/fa';


const NavBar = (props) => {
        return(
            <div className = "MenuBar">
                <div className = {"Menu-Option " + (props.Display === '1' ? 'selected' : 'hid')}
                onClick = {props.SwitchTo.bind(this)} data-id="1"><a>
                <i><FontAwesome.FaUser 
                    size = {20}
                    /> </i>
                    {props.Name1}</a>
                </div>
                <div className = {"Menu-Option " + (props.Display === '2' ? 'selected' : 'hid')}
                onClick = {props.SwitchTo.bind(this)} data-id="2"><a>
                    <i><FontAwesome.FaFileText 
                    size = {20}
                    /></i>
                    {props.Name2}</a></div>
                <div className = {"Menu-Option " + (props.Display === '3' ? 'selected' : 'hid')}
                onClick = {props.SwitchTo.bind(this)} data-id="3"><a>
                     <i><FontAwesome.FaCodeFork
                    size = {20}
                    /></i>
                    {props.Name3}</a></div>
                <div className = {"Menu-Option " + (props.Display === '4' ? 'selected' : 'hid')} 
                onClick = {props.SwitchTo.bind(this)} data-id="4"><a>
                     <i><FontAwesome.FaEnvelope
                    size = {20}
                    /></i>
                    {props.Name4}</a></div>
            </div>
        );
    }
export default NavBar;


