import React, { Component } from 'react';
import NavBar from './Layout/NavigationBar';
import About from './Layout/Pages/About';
import Resume from './Layout/Pages/Resume';
import Information from './Layout/Pages/Information';
import Contact from './Layout/Pages/Contact';

class App extends Component {
  constructor() {
    super();
  this.state = {
    display: [
      { id: 'aboutwefwe', name: 'Home'},
      { id: 'res', name: 'Itinerary'},
      { id: 'proj', name: 'Information'},
      { id: 'con', name: 'Contact'}
            ],
    select: '1',

    color: 'white',

    Highlighted: [
      'red',
       'white',
       'white',
       'white'
    ],
      width:  window.innerWidth,
      height: window.innerHeight
  }
  this.updateDimensions = this.updateDimensions.bind(this);
}
  updateDimensions() {
    
     this.setState({
       height: window.innerHeight, 
       width: window.innerWidth
     });
  }
  
  componentDidMount() {
    window.addEventListener("resize", this.updateDimensions);
    console.log('Welcome to my Portfolio! All code available at : "https://github.com/LucasClaude)"');
  }
    
  SwitchDisplay = (event) =>
  {
    const Display =  event.currentTarget.dataset.id;
    this.setState({select:Display})
  }

  render() {
        return (
         <div className="App">
           <div className ="NavBar">
          <NavBar
              SwitchTo = {this.SwitchDisplay}
              Name1 = {this.state.display[0].name}
              Name2 = {this.state.display[1].name}
              Name3 = {this.state.display[2].name}
              Name4 = {this.state.display[3].name}
              Display = {this.state.select}
              Hi = {this.state.Highlighted[0]}
              size = {this.state.width}
          /> 
         </div>
          { this.state.select === '1' ?
            <About
                Hello = {this.Hello}
                SwitchTo = {this.SwitchDisplay}
            />
          : this.state.select === '2' ?
            <Resume/>
          : this.state.select === '3' ?
            <Information/>
          : <Contact/> }
      </div>
    );
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
  }
}
export default App;



